<template >
  <el-table
      :data="tableData"
      style="width: 100%"
      height="250">
    <el-table-column
        fixed
        prop="id"
        label="id"
        width="150">
    </el-table-column>
    <el-table-column
        prop="account"
        label="姓名"
        width="120">
    </el-table-column>
    <el-table-column
        prop="password"
        label="密码"
        width="120">
    </el-table-column>
    <el-table-column
        prop="permissions"
        label="权限"
        width="120">
      <template slot-scope="scope">
        {{scope.row.permissions == 0?"普通用户":""}}
        {{scope.row.permissions == 1?"管理员":""}}
        {{scope.row.permissions == 2?"超级管理员":""}}
      </template>
    </el-table-column>
  </el-table>
</template>
<script>
import axios from "axios";
export default {
  name: 'UserTabView',
  data() {
    return {
      tableData: [],

    }
  },
  mounted() {
    axios.get("http://93rf36tz6467.vicp.fun:15427/UserData").then(res => {
      this.tableData = res.data.data;
    })
  }

}
</script>
